import * as React from 'react'
import PropTypes from "prop-types"
import { Link, useStaticQuery, graphql } from 'gatsby'
import "bootstrap/dist/css/bootstrap.min.css"
import "bootstrap/dist/js/bootstrap.min"
import { Helmet } from 'react-helmet'
import Nav from './nav'
import SidePanels from './sidePanels'
import Footer from './footer'
import BG from '../images/bg.png';
import "@fontsource/noto-sans-tc/300.css"

const Layout = ({pageTitle, children, containerMaxWidth}) =>{

    const data = useStaticQuery(graphql`
        query{
            site{
                siteMetadata{
                    title
                }
            }
        }
    `)

    return (
        <>
            <Helmet>
                <title>{pageTitle} | {data.site.siteMetadata.title}</title>
                <style>{`body { background-image: url(${BG}); }`}</style>
            </Helmet>

            <div style={{fontFamily: "Noto Sans TC" }}>
            
                <Nav containerMaxWidth={containerMaxWidth} siteName={data.site.siteMetadata.title}/>

                <div className="container" 
                     style={containerMaxWidth?{maxWidth:containerMaxWidth+'px'}:{}}>

                    <div className="row mt-5">
                        {/* left Content/List column */}
                        <div className="col col-md-9">
                            <h2 className="mt-4 mb-3">{pageTitle}</h2>
                            {children}
                        </div>

                        {/* right SidePanels column */}
                        <div className="col col-md-3">
                            <SidePanels/>
                        </div>
                    </div>

                    <div className="row">
                        <div className="col">
                            <Footer/>
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}

Layout.propTypes = {
    children: PropTypes.node.isRequired,
}

export default Layout