import { useStaticQuery, graphql, Link } from "gatsby";
import React from "react";
import {tagPill} from './sidePanels.module.scss';

const SidePanels = ({containerMaxWidth}) =>{
    const data = useStaticQuery(query)

    const [stYmList, setStYmList] = React.useState([]);

    React.useEffect( ()=>{
        const dtList = data.dtList?.distinct
        let ymList = [];
        dtList?.forEach(dt => {
            let ym= dt.substring(0,7)
            if( !ymList.includes(ym) ){
                ymList.push(ym)
            }
            ymList.sort().reverse()
        });
        setStYmList(ymList)
    },[data.dtList?.distinct])

    return (
        <div className="pt-3">
            <div className="card my-4 d-sm-none d-md-block border border-secondary">
                <div className="card-header">貼 文 分 類</div>
                <div className="card-body">
                    <ul>
                        {data?.categoryList?.distinct?.map( cat=> { 
                                return <Link to={`/category/${encodeURI(cat)}`} className="mb-3"  key={cat}>
                                        <li>{cat}</li>
                                    </Link>        
                            }) 
                        }
                    </ul>
                </div>
            </div>
            
            <div className="card my-4 d-sm-none d-md-block border border-secondary">
                <div className="card-header">貼 文 年 月</div>
                <div className="card-body">
                    <ul>
                        {stYmList?.map( ym=> { 
                                return <Link to={`/ym/${encodeURI(ym)}`} className="mb-3"  key={ym}>
                                        <li>{ym}</li>
                                    </Link>        
                            }) 
                        }
                    </ul>
                </div>
            </div>
            
            <div className="card my-4 d-sm-none d-md-block border border-secondary">
                <div className="card-header">貼 文 標 籤</div>
                <div className="card-body">                    
                        {data?.tagList?.distinct?.map( tag=> { 
                                return <Link to={`/tag/${encodeURI(tag)}`} key={tag}
                                        className={`badge rounded-pill bg-primary mx-1 my-3 px-3 text-wrap ${tagPill}`}>
                                        {tag}
                                    </Link>        
                            }) 
                        }
                </div>
            </div>
        </div>
    )
}

export default SidePanels;


const query = graphql`
    query {
        categoryList: allMdx {
            distinct(field: frontmatter___category)
        }

        tagList:allMdx{
            distinct(field: frontmatter___tags)
        }

        dtList: allMdx{
              distinct(field: frontmatter___date)
        }
    }
`