import React from "react";
import { Link,graphql, useStaticQuery } from "gatsby";
import NavBgImg from '../images/shattered-island.gif';

const Nav = ({containerMaxWidth, siteName}) =>{
    
    const query = graphql`
        query {
            categoryList4Nav: allMdx {
                distinct(field: frontmatter___category)
            }
        }
    `
    const data =  useStaticQuery(query)

    return (
        <nav className="navbar fixed-top navbar-expand-lg navbar-dark bg-dark" style={{backgroundImage:`url(${NavBgImg})`}}>
            <div className="container" style={containerMaxWidth?{maxWidth:containerMaxWidth+'px'}:{}}>
                <Link to="/" className="navbar-brand fw-bold">{siteName}</Link>
                <button className="navbar-toggler" type="button" title="展開選單"
                    data-bs-toggle="collapse" data-bs-target="#navbarResponsive" aria-controls="navbarResponsive" 
                    aria-expanded="false"  aria-label="展開選單">
                    <span className="navbar-toggler-icon"></span>
                </button>
                <div className="collapse navbar-collapse" id="navbarResponsive">
                    <ul className="navbar-nav ms-auto"> 
                        <li className="nav-item dropdown d-sm-block d-md-none">
                            <a className="nav-link dropdown-toggle" href="#" role="button" data-bs-toggle="dropdown" aria-expanded="false">
                                文章分類
                            </a>
                            <ul className="dropdown-menu">
                                {
                                    data?.categoryList4Nav?.distinct?.map(cat=>{
                                        return  <li key={cat}>
                                                    <Link to={`/category/${cat}`} className="dropdown-item">{cat}</Link>
                                                </li>
                                    })
                                }
                            </ul>
                        </li>
                        <li className="nav-item me-2">
                            <Link to="/download" className="nav-link border border-white rounded" style={{color:'white'}}>下載</Link>
                        </li>
                        <li className="nav-item">
                            <Link to="/contact" className="nav-link  border border-white rounded" style={{color:'white'}}>與我聯絡</Link>
                        </li>
                    </ul>
                </div>
            </div>
        </nav>)
}

export default Nav
